.board-border {
  border: 10px solid rgb(52, 52, 52);
  border-radius: 15px;
}

.piece-jar {
  width: 80px;
  height: 80px;
  background-color: rgb(65, 65, 65);
  display: flex;
  justify-content: center;
  align-items: center;
}

.queue-jar-small {
  width: 60px;
  height: 60px;
}

.piece-queue {
  display: flex;
  flex-direction: column;
  row-gap: 5px;
}

.board-container {
  margin: 0px auto;
  display: flex;
  column-gap: 10px;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100dvh;
  justify-content: center;
  padding: 10px;
  row-gap: 10px;
  box-sizing: border-box;
}

.button {
  margin: 0px auto;
  padding: 10px;
  font-size: large;
}
