.cell {
  width: 25px;
  height: 25px;
}

.cell-0 {
  background-color: rgb(65, 65, 65);
  border: 1px solid rgb(86, 86, 86);
}

.cell-0-no-border {
  border: 1px solid rgb(65, 65, 65);
}

.cell-1 {
  background-color: aqua;
  border: 1px solid rgb(0, 192, 192);
}

.cell-2 {
  background-color: #001eff;
  border: 1px solid #0017c0;
}

.cell-3 {
  background-color: orange;
  border: 1px solid rgb(198, 129, 0);
}

.cell-4 {
  background-color: yellow;
  border: 1px solid rgb(193, 193, 0);
}

.cell-5 {
  background-color: rgb(187, 3, 187);
  border: 1px solid rgb(151, 1, 151);
}

.cell-6 {
  background-color: greenyellow;
  border: 1px solid rgb(132, 202, 27);
}

.cell-7 {
  background-color: red;
  border: 1px solid rgb(189, 0, 0);
}

.cell-8 {
  background-color: grey;
  border: 1px solid rgb(160, 160, 160);
}

.row {
  display: flex;
}

.board {
  display: flex;
  flex-direction: column;
  width: fit-content;
}
